import React, { useState, useEffect, useRef } from 'react';
import * as SunCalc from 'suncalc';
import { getSunsetDirection, normalizeOrientation } from './utils/sunUtils';
import './App.css';

const App = () => {
  const [position, setPosition] = useState(null);
  const [sunAzimuth, setSunAzimuth] = useState(0);
  const [deviceHeading, setDeviceHeading] = useState(0);
  const [error, setError] = useState(null);
  const [arMode, setArMode] = useState(false);
  const videoRef = useRef(null);
  const [cameraActive, setCameraActive] = useState(false);

  // 1. Obter geolocaliza√ß√£o
  useEffect(() => {
    const handleSuccess = (pos) => {
      setPosition({
        lat: pos.coords.latitude,
        lng: pos.coords.longitude
      });
    };

    const handleError = (err) => {
      setError(err.message || 'Erro ao obter localiza√ß√£o');
    };

    if (!navigator.geolocation) {
      setError('Geolocaliza√ß√£o n√£o suportada');
      return;
    }

    navigator.geolocation.getCurrentPosition(handleSuccess, handleError, {
      enableHighAccuracy: true,
      timeout: 5000
    });
  }, []);

  // 2. Calcular dire√ß√£o do sol
  useEffect(() => {
    if (position) {
      const azimuth = getSunsetDirection(position.lat, position.lng);
      setSunAzimuth(azimuth);
    }
  }, [position]);

  // 3. Configurar sensor de orienta√ß√£o
  useEffect(() => {
    const handleOrientation = (event) => {
      if (event.alpha !== null) {
        let alpha = event.alpha;
        // Corre√ß√£o para iOS
        if (typeof event.webkitCompassHeading !== 'undefined') {
          alpha = event.webkitCompassHeading;
        }
        setDeviceHeading(normalizeOrientation(alpha));
      }
    };

    window.addEventListener('deviceorientation', handleOrientation, true);
    
    return () => {
      window.removeEventListener('deviceorientation', handleOrientation);
    };
  }, []);

  // 4. Listener para Android (adicione aqui)
useEffect(() => {
  if (arMode && videoRef.current) {
    const videoElement = videoRef.current;
    
    const handleCanPlay = () => {
      videoElement.play().catch(e => console.log('Auto-play error:', e));
    };
    
    videoElement.oncanplay = handleCanPlay;
    
    return () => {
      videoElement.oncanplay = null;
    };
  }
}, [arMode]);

// 5. Listener para iOS (adicione aqui)
useEffect(() => {
  if (arMode) {
    const handleTouchStart = () => {
      if (videoRef.current) {
        videoRef.current.play().catch(e => console.log('Touch play error:', e));
      }
    };
    
    document.addEventListener('touchstart', handleTouchStart);
    
    return () => {
      document.removeEventListener('touchstart', handleTouchStart);
    };
  }
}, [arMode]);

// 6. Limpeza ao desmontar o componente (ADICIONE AQUI)
useEffect(() => {
  return () => {
    if (videoRef.current?.srcObject) {
      const tracks = videoRef.current.srcObject.getTracks();
      tracks.forEach(track => track.stop());
      videoRef.current.srcObject = null;
    }
  };
}, []);

  // 4. Ativar modo AR
  const activateAR = async () => {
    try {

       // Verificar se j√° existe um stream ativo
    if (videoRef.current?.srcObject) {
      const tracks = videoRef.current.srcObject.getTracks();
      tracks.forEach(track => track.stop());
    }

      // 1. Configura√ß√µes otimizadas para mobile
      const constraints = {
        video: {
          facingMode: 'environment',
          width: { ideal: 1280 },
          height: { ideal: 720 }
        }
      };
  
      // 2. Solicitar stream de v√≠deo
      const stream = await navigator.mediaDevices.getUserMedia(constraints);
      
      // 3. Configurar elemento de v√≠deo
      if (videoRef.current) {
        videoRef.current.srcObject = stream;
        videoRef.current.playsInline = true;
        videoRef.current.muted = true;
        
        // Tentativa principal de play
        const playWithRetry = (attempt = 0) => {
          videoRef.current.play()
            .then(() => setArMode(true))
            .catch(err => {
              if (attempt < 3) {
                setTimeout(() => playWithRetry(attempt + 1), 300 * (attempt + 1));
              } else {
                setError('Falha ao iniciar c√¢mera. Toque na tela para tentar novamente.');
                setArMode(false);
              }
            });
        };
        
        playWithRetry();
      }
    } catch (err) {
      console.error('AR Error:', err);
      setError(`Erro: ${err.message}`);
      setArMode(false);
    }
  };

  // 5. Calcular √¢ngulo relativo
  const calculateRelativeAngle = () => {
    return (sunAzimuth - deviceHeading + 360) % 360;
  };

  // 6. Verificar alinhamento
  const isAligned = Math.abs(calculateRelativeAngle()) < 5;

  return (
    <div className="app-container">
      {arMode && (
        <video 
        ref={videoRef}
        className="ar-video"
        autoPlay
        playsInline
        muted
        webkit-playsinline="true"
        x-webkit-airplay="allow"
      />
      )}

      <h1>üåÖ Ca√ßador de P√¥r do Sol</h1>
      
      <button 
  className={`ar-button ${cameraActive ? 'active' : ''}`}
  onClick={async () => {
    if (arMode) {
      // Desativa√ß√£o limpa
      if (videoRef.current?.srcObject) {
        const tracks = videoRef.current.srcObject.getTracks();
        tracks.forEach(track => track.stop());
        videoRef.current.srcObject = null;
      }
      setArMode(false);
      setCameraActive(false);
    } else {
      await activateAR();
    }
  }}
>
  {arMode ? 'üì∑ Desativar AR' : 'üåç Modo AR'}
</button>

      {error && <div className="error-banner">{error}</div>}

      <div className={`compass-wrapper ${arMode ? 'ar-active' : ''}`}>
        <div className="compass">
          <div 
            className="direction-arrow"
            style={{ transform: `rotate(${calculateRelativeAngle()}deg)` }}
          >
            <div className="sun-indicator">‚òÄÔ∏è</div>
          </div>
          <div className="alignment-marker"></div>
        </div>

        <div className="info-panel">
          <p>‚è± Hor√°rio do p√¥r do sol: {
            position && 
            new Date(SunCalc.getTimes(new Date(), position.lat, position.lng).sunset)
              .toLocaleTimeString()
          }</p>
          <p>üß≠ Dire√ß√£o: {sunAzimuth.toFixed(1)}¬∞</p>
          <div className={`alignment-feedback ${isAligned ? 'aligned' : ''}`}>
            {isAligned ? '‚≠ê ALINHADO! ‚≠ê' : 'Gire o dispositivo... ‚û°Ô∏è'}
          </div>
        </div>
      </div>
    </div>
  );
};

export default App;